import React from 'react';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';
import styles from "../styles/custom-styles.sass";;



const ListUsers = ({ id, firstName, lastName, rating, photoURL, amount, idVk }) => {
    const urlUser = 'https://vk.com/id' + idVk;

    return (
        <Div className='card'>
            <span className='listNumber'>{id}</span>
            <Link href={urlUser} target="_blank">
                <Cell
                    className='myItem'
                    before={photoURL ? <Avatar src={photoURL} /> :
                        <Avatar src={'https://vk.com/images/camera_200.png'} />}
                    description={
                        <Div className="div">
                            <Div className="div">
                            <Icon12Fire fill="red"/>
                                 {`${"       "}${rating}`} 
                            </Div>
                            <Div className="div">
                                DurkaCoins: {amount}
                            </Div>
                        </Div>
                    }
                    // asideContent={<Icon24FavoriteOutline fill="black" />}
                    // before={<Icon24FavoriteOutline/>}
                    // indicator={rating}
                // indicator={<Icon24FavoriteOutline fill="black"/>}
                >
                    {firstName + " " + lastName}
                </Cell>
            </Link>
        </Div>
    );
};

export default ListUsers;

